  margin: 0 0.5rem 0 0;
  padding: 1px 8px 3px 0;
  padding: 0.0625rem 0.5rem 0.1875rem 0; }

.line {
  margin-bottom: 1em !important; }

section[role="document"] {
  margin-top: 1.5rem; }

figure {
  margin: 0;
  padding: 0; }

figcaption {
  margin-bottom: 1.25rem;
  margin-top: 0.625rem;
  color: #6f6f6f; }

.alignleft {
  float: left; }

.alignright {
  float: right; }

.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto; }

blockquote.alignleft,
img.alignleft {
  margin: 0.4375rem 1.5rem 0.4375rem 0; }

.wp-caption.alignleft {
  margin: 0.4375rem 0.875rem 0.4375rem 0; }

blockquote.alignright,
img.alignright {
  margin: 0.4375rem 0 0.4375rem 1.5rem; }

.genericwrapper {
  padding: 2.5rem 0;
  border-top: 1px solid #ccc; }

.main-content-wrap {
  background: #eee; }

.content-image-box {
  padding: 0 15%; }
  @media print, screen and (min-width: 40em) {
    .content-image-box {
      padding: 0 1.875rem; } }
  @media print, screen and (min-width: 64em) {
    .content-image-box {
      padding: 0 1.25rem; } }

.content-image-box-area-wrap {
  background: #AD0F35; }

.content-image-box {
  margin-bottom: 1em; }

.content-text-box {
  padding: 1em; }
  .content-text-box h3 {
    font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 1.125rem;
    color: #666;
    margin-bottom: 0.3125rem;
    font-weight: bold; }
  .content-text-box p:last-child {
    margin-bottom: 0; }

.logos-wrapper {
  background: #fff; }

.site-wide-updates-container {
  background: #eee; }

